.portfolio-container{
    width: 90%;
    margin: 5%;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    

}
.portfolio-container h3{
    text-align: center;
    font-weight: 700;
    font-size: 3rem;
    width: 100%;
   
   
}


.port-box{
    width: 30%;
    height: 60vh;
    margin: auto;
    margin-top: 1rem;
    background-color: white;
    border-radius: 5px;
   box-shadow: 1px 1px 10px 1px #8888;
}
@media(max-width:900px){
    .port-box{
        width: 95%;
        margin: auto;
    }
}
/* @media(max-width:600px){
    .port-box{
        width: 95%;
        margin: auto;
    }
} */
.port-box img:hover{
    transform: scale(1.01);
    transition: all 0.1s ease-in-out;
}
.port-box h5{
    margin: 1rem;
    
}
