nav{
    height: 12vh;
    background-color:rgb(8, 7, 7);
    color: white;
    display: flex;
    justify-content: space-between;
    position: fixed;
    width: 100%;
}
nav .logo{
    padding: 20px;
    font-weight: 500;
}
.title{
    color: blue;
}
nav ul{
    display: flex;
    gap: 20px;
    padding: 20px;
    margin-right: 10%;
}
nav ul li{
    list-style: none;
    cursor: pointer;
}
nav ul li:hover, .active{
    border-bottom: 2px solid white;
}
nav img{
display: block;
margin: 15px;
}
.sidebar{
    width: 500px;
    height: 100vh;
    background-color: black;
    z-index: 999;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: none;
    
   
}
.sidebar ul{
    display: flex;
    flex-direction: column;
    gap: 50px;  
}
@media (min-width: 900px){
    .sidebar{
        display: none;
    }
    nav img {
        display: none;
    }
}
@media (max-width: 900px){
    .desktop{
        display: none;
    }
    .sidebar{
        display: none;
    }
    img .menu-btn{
        display: block;
    }


}
@media (max-width: 500px){
    .sidebar{
        width: 100%;
    }
}